import React, { Component } from 'react';
import MemorandumDataClass from './Table'
import Form from './Form'
import { Redirect } from 'react-router-dom';



class Home extends Component {

  state = {
      memorandum: [{"attn":"Loading...", "from":"Loading...", "subject":"Loading...", "para1": "Loading...", "unit": "Loading...", "date": "Loading...", "dutytitle": "Loading...", "rank": "Loading...", "writersname": "Loading...", "branch": "Loading..." }],
      toDashboard: false,
  };

  handleSubmit = memorandum => {
      this.setState({memorandum: [memorandum]});
      var MEMOVAR = memorandum;
      sessionStorage.setItem(0, memorandum);
      console.log('memovar ' + MEMOVAR);
      console.log('memovar 1 ' + MEMOVAR.attn);
      console.log('memovar 2 ' + MEMOVAR.from);
      sessionStorage.setItem(1, MEMOVAR);
      sessionStorage.setItem("attn", MEMOVAR.attn);
      sessionStorage.setItem("from", MEMOVAR.from);
      sessionStorage.setItem("subject", MEMOVAR.subject);
      sessionStorage.setItem("para1", MEMOVAR.para1);
      sessionStorage.setItem("unit", MEMOVAR.unit);
      sessionStorage.setItem("date", MEMOVAR.date);
      sessionStorage.setItem("dutytitle", MEMOVAR.dutytitle);
      sessionStorage.setItem("rank", MEMOVAR.rank);
      sessionStorage.setItem("writersname", MEMOVAR.writersname);
      sessionStorage.setItem("branch", MEMOVAR.branch);

      console.log("All storage actions have been completed, moving to memo page");
      this.setState({toDashboard: true});

  }



  componentDidMount(){
    setTimeout(() => {
      var LSGETATTN = sessionStorage.getItem("attn");
      var LSGETFROM = sessionStorage.getItem("from");
      var LSGETSUBJECT = sessionStorage.getItem("subject");
      var LSGETPARA = sessionStorage.getItem("para1");
      var LSGETUNIT = sessionStorage.getItem("from");
      var LSGETDATE = sessionStorage.getItem("subject");
      var LSGETDUTYTITLE = sessionStorage.getItem("dutytitle");
      var LSGETRANK = sessionStorage.getItem("rank");
      var LSGETWRITERSNAME = sessionStorage.getItem("writersname");
      var LSGETBRANCH = sessionStorage.getItem("branch");
      console.log(this.state);

      this.setState({memorandum: [{attn: LSGETATTN,
        from: LSGETFROM,
        subject: LSGETSUBJECT,
        para1: LSGETPARA,
        unit: LSGETUNIT,
        date: LSGETDATE,
        dutytitle: LSGETDUTYTITLE,
        rank: LSGETRANK,
        writersname: LSGETWRITERSNAME,
        branch: LSGETBRANCH


      }]})
      console.log(this.state);
    }, 1000)
  }

  setTest= memorandum =>{
          this.setState({memorandum: [{attn: "TSGT AUSTIN R BEATY", from: "TSGT AUSTIN R BEATY", subject: "The Creation of Memorandums", para1: "The following information on writing a memorandum comes from the AFH 33-337 Tongue and Quill, pages 183-186.  This template matches the format for the header on page 183, and the body on page 185.  The letterhead is in Arial with the “DEPARTMENT OF THE AIR FORCE” bolded, and the unit name un-bolded.  Addresses in the “MEMORADUM FOR” and “FROM”  are only if the letter for an off station addressee and are not required—however, office symbols are!", unit: "31ST FIGHTER WING (USAFE)", date: "DATE", dutytitle: "SYSTEM ADMINISTRATOR"}]})
      sessionStorage.setItem("attn", "TSGT AUSTIN R BEATY");
      sessionStorage.setItem("from", "TSGT AUSTIN R BEATY");
      sessionStorage.setItem("subject", "The Creation of Memorandums");
      sessionStorage.setItem("para1", "The following information on writing a memorandum comes from the AFH 33-337 Tongue and Quill, pages 183-186.  This template matches the format for the header on page 183, and the body on page 185.  The letterhead is in Arial with the “DEPARTMENT OF THE AIR FORCE” bolded, and the unit name un-bolded.  Addresses in the “MEMORADUM FOR” and “FROM”  are only if the letter for an off station addressee and are not required—however, office symbols are!");
      sessionStorage.setItem("unit", "31ST FIGHTER WING (USAFE)");
      sessionStorage.setItem("date", "DATE");
      sessionStorage.setItem("dutytitle", "SYSTEM ADMINISTRATOR");
      sessionStorage.setItem("rank", "TSgt");
      sessionStorage.setItem("writersname", "AUSTIN R. BEATY");
      sessionStorage.setItem("branch","USAF");

  }


  render() {
    const { memorandum } = this.state;
    if (this.state.toDashboard === true){
      return (<Redirect to='memorandum' />)
    }
    return (
      <div className="container">
      <h2>Memorandum Builder</h2>
          <MemorandumDataClass memorandumData={memorandum}  />
          <h3>Add New</h3>
          <Form handleSubmit={this.handleSubmit} />
          <button onClick={this.setTest} type="submit">
              Set Test
          </button>
      </div>

    );
  }
}
export default Home;
