import React, {Component} from 'react';



class Form extends Component {
    constructor(props) {
        super(props);

        this.initialState = {
            attn: 'Insert Receiver of Memorandum Here',
            from: 'Insert Your Information Here',
            subject: 'Insert Subject Here',
            para1: 'Insert first Paragraph',
            unit: 'Type your Unit Here',
            date: 'Select Date of Memorandum',
            dutytitle: 'Enter your Duty Title Here',
            rank: 'Select Your Rank',
            writersname: 'What is Your Name? FIRST MI. LAST',
            branch: 'Select Your Branch of Service'
        };

        this.state = this.initialState;
    }



    handleChange = event => {
        const { name, value } = event.target;
        this.setState({
            [name] : value
        });

    }

    handleInputChange = e => {
    this.setState({
        [e.target.name]: e.target.value
    });
}

    onFormSubmit = (event) => {
        event.preventDefault();
        this.props.handleSubmit(this.state);
        this.setState(this.initialState);
    }

    clearForm = memorandum => {
        console.log("clearForm button pressed");
        this.setState({memorandum: [{
            attn:" ",
            from:" ",
            subject:" ",
            para1: " ",
            unit: " ",
            date: " ",
            dutytitle: " ",
            rank: " ",
            writersname: " ",
            branch: " "

        }]});
        sessionStorage.clear();
        alert("The information in your memorandum has been cleared.");
        window.location.reload();
    }

    render() {
        const { attn, from, subject, para1, unit, date, dutytitle, rank, writersname, branch } = this.state;
        //Identifies type and DOM target for each <td> type located on Table.js

        return (
          <div class="w3-padding w3-round-xlarge w3-modal-content w3-card-4 w3-animate-zoom" >
            <form class="w3-padding" id="memorandumForm" onSubmit={this.onFormSubmit}>
                <input
                    type="text"
                    name="attn"
                    id="attn"
                    placeholder={attn}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="from"
                    id="from"
                    placeholder={from}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="subject"
                    id="subject"
                    placeholder={subject}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="para1"
                    id="para1"
                    placeholder={para1}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="unit"
                    id="unit"
                    placeholder={unit}
                    onChange={this.handleChange} />
                <input
                    type="date"
                    name="date"
                    id="date"
                    pattern="ddmmyyyy"
                    placeholder={date}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="dutytitle"
                    id="dutytitle"
                    placeholder={dutytitle}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="rank"
                    id="rank"
                    placeholder={rank}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="writersname"
                    id="writersname"
                    placeholder={writersname}
                    onChange={this.handleChange} />
                <input
                    type="text"
                    name="branch"
                    id="branch"
                    placeholder={branch}
                    onChange={this.handleChange} />
            </form>
            <button form="memorandumForm" onClick="navigateForward" type="submit">
                Submit
            </button>
            <button style={{marginLeft: 5}} onClick={this.clearForm} >
                Clear Form
            </button>
          </div>
        );
    }
}

export default Form;
