import React, {Component} from 'react';
import jsPDF from 'jspdf'

var pageWidth = 8.5,
 lineHeight = 1.15,
 margin = 1,
 maxLineWidth = 6.5,
 fontSize = 12,
 ptsPerInch = 72,
 oneLineHeight = (fontSize * lineHeight) / ptsPerInch,
 wrapHeight = 0,
 textLines = 0
var LSGETATTN;
var LSGETFROM;
var LSGETSUBJECT;
var LSGETPARA;
var LSGETUNIT;
var LSGETDATE;
var LSGETDUTYTITLE;
var LSGETRANK;
var LSGETWRITERSNAME;
var LSGETBRANCH;
let DATEHEIGHT = 1.75 + oneLineHeight;
let ATTNHEIGHT = DATEHEIGHT + oneLineHeight;
let FROMHEIGHT = ATTNHEIGHT + (oneLineHeight * 2);
let SUBJECTHEIGHT = FROMHEIGHT + (oneLineHeight * 2);
let PARAONEHEIGHT = SUBJECTHEIGHT + (oneLineHeight * 2);

var doc = new jsPDF({orientation: 'p', unit: "in", format: 'letter'});


function addWrappedText({text, textWidth, doc, fontSize = 12, fontType = 'normal', lineSpacing = .19, xPosition = 10, initialYPosition = 10, pageWrapInitialYPosition = 10}) {

  //Split Text var textLines = doc.splitTextToSize(text, maxLineWidth);
  var PARANEWLINES = doc.splitTextToSize("1.  " + LSGETPARA + '\n \n' + '2.  ' + LSGETPARA + LSGETPARA+ LSGETPARA + LSGETPARA+ LSGETPARA, maxLineWidth);
  //doc.text(textLines, margin, margin + 2 * oneLineHeight)
  //doc.text(PARANEWLINES, margin, PARAONEHEIGHT);
  //var textHeight = (textLines.length * fontSize * lineHeight) / ptsPerInch;
  var PARANEWLINESTEXTHEIGHT = (PARANEWLINES.length * fontSize * lineHeight) / ptsPerInch;
  //doc.text("height" margin, margin + oneLineHeight)





textLines = PARANEWLINES; // Split the text into lines
wrapHeight = (textLines.length * fontSize * lineHeight) / ptsPerInch;
var pageHeight = doc.internal.pageSize.height - 1;        // Get page height, we'll use this for auto-paging. TRANSLATE this line if using units other than `pt`
var cursorY = initialYPosition;

textLines.forEach(lineText => {
  if (cursorY > pageHeight) { // Auto-paging
    doc.addPage();
    cursorY = pageWrapInitialYPosition;
    var SIGNATUREHEIGHT = PARAONEHEIGHT + PARANEWLINESTEXTHEIGHT + (oneLineHeight * 5);
    doc.text(LSGETWRITERSNAME + ', ' + LSGETRANK + ', ' + LSGETBRANCH, 4.5, SIGNATUREHEIGHT );
    doc.text(LSGETDUTYTITLE, 4.5, SIGNATUREHEIGHT + oneLineHeight );

  }
  doc.text(xPosition, cursorY, lineText);
  cursorY += lineSpacing;
})
}

class GenerateMemorandum extends Component {

  state = {
    dateHeight: 1.9166,
    attnHEIGHT: this.dateHeight,
  }

  generateImage(){
    doc.addImage("data:image/PNG;base64,", "PNG", .4, .4, 1, 1)
  }




  generateMemorandum=()=>{

    this.generateImage();
    doc.setFontStyle("bold");
    doc.setFontSize(12);
    doc.text("DEPARTMENT OF THE AIR FORCE", 4.25,.845, null, null, "center");
    doc.setFontStyle("normal");
    doc.text(LSGETUNIT, 4.25, 1.039, "center");
    doc.setFont('Times New Roman');
    doc.text(LSGETDATE, 7.5, DATEHEIGHT, null, null, "right");
    doc.text("MEMORANDUM FOR  " + LSGETATTN, 1, ATTNHEIGHT);
    doc.text("FROM:  " + LSGETFROM, 1, FROMHEIGHT);
    doc.text("SUBJECT:  " + LSGETSUBJECT, 1, SUBJECTHEIGHT);

    //Split Text var textLines = doc.splitTextToSize(text, maxLineWidth);
    var PARA1LINES = doc.splitTextToSize("1.  " + LSGETPARA, maxLineWidth);
    //doc.text(textLines, margin, margin + 2 * oneLineHeight)
    doc.text(PARA1LINES, margin, PARAONEHEIGHT);
    //var textHeight = (textLines.length * fontSize * lineHeight) / ptsPerInch;
    var PARA1TextHeight = (PARA1LINES.length * fontSize * lineHeight) / ptsPerInch;
    //doc.text("height" margin, margin + oneLineHeight)
    doc.text("Text Height is " + PARA1TextHeight, 1, 9);
    var SIGNATUREHEIGHT = PARAONEHEIGHT + PARA1TextHeight + (oneLineHeight * 5);
    doc.text(LSGETWRITERSNAME + ', ' + LSGETRANK + ', ' + LSGETBRANCH, 4.5, SIGNATUREHEIGHT );
    doc.text(LSGETDUTYTITLE, 4.5, SIGNATUREHEIGHT + oneLineHeight );



    //doc.output('dataurlnewwindow');
    //doc.save('a4.pdf');
    var string = doc.output('datauristring');
    var embed = "<embed width='100%' height='100%' src='" + string + "'/>"
    var x = window.open();
    x.document.open();
    x.document.write(embed);
    x.document.close();

  }

  fillVariables(){
    LSGETATTN = sessionStorage.getItem("attn");
    LSGETFROM = sessionStorage.getItem("from");
    LSGETSUBJECT = sessionStorage.getItem("subject");
    LSGETPARA = sessionStorage.getItem("para1");
    LSGETUNIT = sessionStorage.getItem("unit");
    LSGETDATE = sessionStorage.getItem("date");
    LSGETDUTYTITLE = sessionStorage.getItem("dutytitle");
    LSGETRANK = sessionStorage.getItem("rank");
    LSGETWRITERSNAME = sessionStorage.getItem("writersname");
    LSGETBRANCH = sessionStorage.getItem("branch");
  }


  render() {
    this.fillVariables();
        console.log('attn ' + ATTNHEIGHT+ 'from ' + FROMHEIGHT+ 'subject: ' + SUBJECTHEIGHT + 'PARA: ' + PARAONEHEIGHT)
    return (
      <div>
      <button onClick={this.generateMemorandum} type="submit">
          Generate PDF
      </button>
      <button onClick={this.generateMemorandumnew} type="submit">
          Generate PDF TEST
      </button>
      </div>
    )
  }
}


export default GenerateMemorandum;
//render(<App />, `${__dirname}/example.docx`)
